@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&family=Raleway:ital,wght@0,300;0,700;1,400&display=swap");

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(180deg, #ac9eff 0%, #000000 100%);
}

.title {
  font-family: "Comfortaa", cursive;
  pointer-events: none;
}

.card-container {
  background-color: #ac9eff;
  height: 60%;
  width: 100%;
  max-width: 60rem;
  font-family: "Raleway", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: -8px -13px 70px rgba(0, 0, 0, 0.25);
  border-radius: 0.5rem;
  position: relative;
  margin-bottom: 5rem;
  margin-top: 5rem;
}

.quote {
  font-weight: bold;
  font-size: 2rem;
  max-width: 46.1875rem;
  animation: load-text 400ms ease-in;
  animation-fill-mode: forwards;
  line-height: 3rem;
}

.actions {
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  justify-content: space-between;
  padding: 4rem 5rem;
  font-size: 2rem;
}

.author {
  opacity: 0;
  animation: load-author 500ms ease-in 500ms;
  animation-fill-mode: forwards;
}

.twitter {
  color: rgb(78, 78, 78);
  cursor: pointer;
  transition: color 200ms ease;
}

.twitter:hover {
  color: black;
}

.btn {
  padding: 1rem 2rem;
  border-radius: 2.375rem;
  border: none;
  background-color: #ccc4fb;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 200ms ease-in, transform 200ms ease-in;
}

.btn:hover {
  background-color: #7861fa;
  transform: translateY(-5px);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
}

@keyframes load-text {
  0% {
    opacity: 0;
    transform: translateY(-1rem);
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes load-author {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
